
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import resultList from './resultList'
import recordList from './recordList'
import AppCode from '@/components/common/Appcode'
import { guid } from '@/storage'
import { getVoteInfo, getVdlist, getVoteCode } from 'api'
export default {
data () {
return {
id: this.$route.params.id,
stickyTop: guid.get() ? 0 : 46,
info: '',
code: '',
tabActive: 'result',
list: [],
page: 1,
noData: true,
loading: false,
resultList: []
}
},
computed: {
Radio () {
return this.info.Questions[0].Radio
},
status () {

let status = ''
switch (this.info.Status) {
case '0':
status = '未开始'
break
case '1':
status = '进行中'
break
case '2':
status = '已结束'
break
case '3':
status = '项目创建中'
break
default:
status = ''
break
}
return status
}
},
created () {
this.getInfo()
},
methods: {
changeItem (key) {
this.tabActive = key
if (key === 'record' && !this.list.length) {
this.getList()
}
if (key === 'code' && !this.code) {
this.getCode()
}
},
getInfo () {
if (this.loading) {
return
}
this.loading = true
getVoteInfo({voteId: this.id})
.then(res => {
this.info = res.data
this.resultList = res.data.Questions[0].Result
})
.finally(() => {
this.loading = false
})
},
getList () {
if (this.loading) {
return
}
this.loading = true
getVdlist({voteId: this.id})
.then(res => {
this.setList(res)
})
.finally(() => {
this.loading = false
})
},
setList (res) {
let list = res.data
if (this.page <= 1) {
this.list = list
} else {
this.list.push(...list)
}
this.noData = (list.length < 10)
this.loading = false
},
loadMore () {
this.noData = true
this.page += 1
this.getList()
},
getCode () {
getVoteCode({voteId: this.id})
.then(res => {
this.code = res.data
})
}
},
components: {
XHeader,
Tab,
TabItem,
resultList,
recordList,
Sticky,
AppCode,
LoadMore
}
}



